.code {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  background: #1b2b34; }
  @media (max-width: 767px) {
    .code {
      grid-template-columns: 1fr; } }

.editor__header {
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  padding: 5px;
  grid-gap: 5px;
  background: rgba(0, 0, 0, 0.1); }

.editor {
  display: grid;
  grid-template-rows: auto 1fr; }
  @media (max-width: 767px) {
    .editor {
      grid-template-rows: auto minmax(300px, 1fr); } }

.editor__code {
  display: grid;
  grid-template-columns: auto 1fr; }

.editor__number {
  display: block;
  padding: 0 10px; }

.editor__input {
  resize: none;
  font-family: 'Titillium Web', sans-serif;
  background: none;
  border: 0;
  color: grey;
  font-style: 1rem;
  line-height: 1.20rem; }

.pen {
  background: black;
  border-bottom: 5px solid #343436;
  color: #ffffff;
  padding: 10px; }

.preview__iframe {
  width: 100%;
  height: 100%;
  border: none; }

.settings {
  padding: 5px;
  background: black;
  border-top: 1px solid grey; }

html {
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
  font-weight: normal;
  color: #272727;
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.07); }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  padding: 5px;
  margin: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  margin: 0;
  font-size: 1.5em; }

/*CSS GRID LAYOUT*/
.codepen {
  display: grid;
  grid-template-rows: auto 1fr 1fr auto;
  height: 100vh;
  color: #ffffff; }

.codepen > * {
  border: 1px solid black; }

.pen {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-flow: column;
  align-items: center;
  grid-gap: 10px; }
  @media (max-width: 767px) {
    .pen {
      grid-auto-flow: row;
      justify-items: start; } }

.preview {
  display: grid;
  grid-template-columns: 1fr; }

/*Buttons*/
.button {
  background: #343436;
  border: 0;
  color: white;
  padding: 10px;
  border-radius: 5px;
  font-size: 0.9rem;
  position: relative; }

.button--small {
  font-size: 0.75rem;
  padding: 4px; }

.button--dirty::before {
  background: #ffc600;
  display: block;
  content: "";
  height: 2px;
  width: calc(100% - 6px);
  position: absolute;
  left: 3px;
  top: 4px; }
